/**
 * Data Fields Persistence
 *
 * Handles saving and loading data field configurations to localStorage.
 * Supports:
 * - Activity-based screen configurations
 * - Field preferences (alerts, voice announcements)
 * - Migration from legacy settings
 *
 * @module data-fields/persistence
 */

import type { ActivityScreensConfig, DataFieldSettings, FieldPreference, ActivityType } from './types.js';
import {
    DEFAULT_DATA_FIELD_SETTINGS,
    migrateFromLegacySettings,
    getDefaultScreensForActivity,
} from './defaults.js';

/** LocalStorage key for data field settings */
const STORAGE_KEY = 'bpt-data-fields';

/** LocalStorage key for legacy settings (for migration) */
const LEGACY_SETTINGS_KEY = 'bpt-settings';

/** Current schema version for data migration */
const SCHEMA_VERSION = 2;

/**
 * Stored settings format with version for migrations
 */
interface StoredSettings {
    version: number;
    settings: DataFieldSettings;
}

// ============================================================================
// Load Functions
// ============================================================================

/**
 * Load data field settings from localStorage
 *
 * @returns The loaded settings or defaults if none exist
 */
export function loadDataFieldSettings(): DataFieldSettings {
    try {
        const stored = localStorage.getItem(STORAGE_KEY);

        if (stored) {
            const parsed = JSON.parse(stored) as StoredSettings;

            // Handle version migrations
            const migrated = migrateStoredSettings(parsed);

            // Validate loaded settings
            if (validateSettings(migrated)) {
                return migrated;
            }

            console.warn('[DataFields] Invalid stored settings, using defaults');
            // Overwrite invalid settings to prevent persistent errors
            saveDataFieldSettings({ ...DEFAULT_DATA_FIELD_SETTINGS });
            return { ...DEFAULT_DATA_FIELD_SETTINGS };
        }

        // Try to migrate from legacy settings
        const legacyStored = localStorage.getItem(LEGACY_SETTINGS_KEY);
        if (legacyStored) {
            try {
                const legacy = JSON.parse(legacyStored);
                const migrated = migrateFromLegacySettings(legacy);
                saveDataFieldSettings(migrated);
                return migrated;
            } catch {
                console.warn('[DataFields] Failed to migrate legacy settings');
            }
        }

        return { ...DEFAULT_DATA_FIELD_SETTINGS };
    } catch (error) {
        console.error('[DataFields] Error loading settings:', error);
        return { ...DEFAULT_DATA_FIELD_SETTINGS };
    }
}

/**
 * Load screens configuration for a specific activity type
 */
export function loadActivityScreens(activityType: ActivityType): ActivityScreensConfig {
    const settings = loadDataFieldSettings();
    return settings.activityScreens[activityType] ?? getDefaultScreensForActivity(activityType);
}

/**
 * Load field preferences for a specific field
 */
export function loadFieldPreference(fieldId: string): FieldPreference | null {
    const settings = loadDataFieldSettings();
    return settings.fieldPreferences[fieldId] ?? null;
}

// ============================================================================
// Save Functions
// ============================================================================

/**
 * Save data field settings to localStorage
 */
export function saveDataFieldSettings(settings: DataFieldSettings): boolean {
    try {
        const stored: StoredSettings = {
            version: SCHEMA_VERSION,
            settings,
        };

        localStorage.setItem(STORAGE_KEY, JSON.stringify(stored));
        return true;
    } catch (error) {
        console.error('[DataFields] Error saving settings:', error);
        return false;
    }
}

/**
 * Save screens configuration for a specific activity type
 */
export function saveActivityScreens(activityType: ActivityType, config: ActivityScreensConfig): boolean {
    const settings = loadDataFieldSettings();
    settings.activityScreens[activityType] = config;
    return saveDataFieldSettings(settings);
}

/**
 * Save field preferences for a specific field
 */
export function saveFieldPreference(fieldId: string, preference: FieldPreference): boolean {
    const settings = loadDataFieldSettings();
    settings.fieldPreferences[fieldId] = preference;
    return saveDataFieldSettings(settings);
}

/**
 * Update the active screen index for an activity type
 */
export function saveActiveScreenIndex(activityType: ActivityType, screenIndex: number): boolean {
    const settings = loadDataFieldSettings();
    const config = settings.activityScreens[activityType];

    if (!config) {
        return false;
    }

    config.activeScreenIndex = Math.max(0, Math.min(screenIndex, config.screens.length - 1));
    return saveDataFieldSettings(settings);
}

// ============================================================================
// Reset Functions
// ============================================================================

/**
 * Reset all settings to defaults
 */
export function resetToDefaults(): boolean {
    try {
        localStorage.removeItem(STORAGE_KEY);
        return true;
    } catch (error) {
        console.error('[DataFields] Error resetting settings:', error);
        return false;
    }
}

/**
 * Reset screens configuration for a specific activity type to defaults
 */
export function resetActivityToDefault(activityType: ActivityType): boolean {
    const settings = loadDataFieldSettings();
    settings.activityScreens[activityType] = getDefaultScreensForActivity(activityType);
    return saveDataFieldSettings(settings);
}

// ============================================================================
// Export/Import Functions
// ============================================================================

/**
 * Export settings as JSON string for backup
 */
export function exportSettings(): string {
    const settings = loadDataFieldSettings();
    return JSON.stringify(settings, null, 2);
}

/**
 * Import settings from JSON string
 *
 * @returns true if import was successful
 */
export function importSettings(jsonString: string): boolean {
    try {
        const parsed = JSON.parse(jsonString) as DataFieldSettings;

        if (!validateSettings(parsed)) {
            console.error('[DataFields] Invalid settings format');
            return false;
        }

        return saveDataFieldSettings(parsed);
    } catch (error) {
        console.error('[DataFields] Error importing settings:', error);
        return false;
    }
}

/**
 * Export screens configuration for a specific activity as JSON
 */
export function exportActivityScreens(activityType: ActivityType): string | null {
    const config = loadActivityScreens(activityType);
    if (!config) {
        return null;
    }
    return JSON.stringify(config, null, 2);
}

/**
 * Import screens configuration for an activity from JSON string
 */
export function importActivityScreens(activityType: ActivityType, jsonString: string): ActivityScreensConfig | null {
    try {
        const parsed = JSON.parse(jsonString) as ActivityScreensConfig;

        if (!validateActivityScreensConfig(parsed)) {
            console.error('[DataFields] Invalid screens configuration format');
            return null;
        }

        // Ensure the activity type matches
        parsed.activityType = activityType;

        // Save the imported configuration
        if (saveActivityScreens(activityType, parsed)) {
            return parsed;
        }

        return null;
    } catch (error) {
        console.error('[DataFields] Error importing screens:', error);
        return null;
    }
}

// ============================================================================
// Migration Functions
// ============================================================================

/**
 * Migrate stored settings to current schema version
 */
function migrateStoredSettings(stored: StoredSettings): DataFieldSettings {
    let settings = stored.settings as any; // Use any to handle legacy structure
    let version = stored.version;

    // Apply migrations in order
    while (version < SCHEMA_VERSION) {
        switch (version) {
            case 0:
                // v0 -> v1: Add fieldPreferences if missing
                settings.fieldPreferences = settings.fieldPreferences ?? {};
                version = 1;
                break;
            case 1:
                // v1 -> v2: Migrate from profiles to activity-based screens
                if (settings.profiles && Array.isArray(settings.profiles)) {
                    const activityScreens: Record<string, ActivityScreensConfig> = {};

                    // Convert each profile to activity-based configuration
                    for (const profile of settings.profiles) {
                        const activityType = profile.activityType || 'cycling';
                        activityScreens[activityType] = {
                            activityType,
                            screens: profile.screens || [],
                            activeScreenIndex: profile.activeScreenIndex || 0,
                        };
                    }

                    // Fill in missing activity types with defaults
                    const defaultActivityTypes: ActivityType[] = ['cycling', 'running', 'indoor', 'walking', 'custom'];
                    for (const type of defaultActivityTypes) {
                        if (!activityScreens[type]) {
                            activityScreens[type] = getDefaultScreensForActivity(type);
                        }
                    }

                    settings.activityScreens = activityScreens;
                    delete settings.profiles;
                    delete settings.activeProfileId;
                }
                version = 2;
                break;
            // Add future migrations here
        }
    }

    return settings as DataFieldSettings;
}

// ============================================================================
// Validation Functions
// ============================================================================

/**
 * Validate complete settings object
 */
function validateSettings(settings: unknown): settings is DataFieldSettings {
    if (!settings || typeof settings !== 'object') {
        return false;
    }

    const s = settings as DataFieldSettings;

    if (!s.activityScreens || typeof s.activityScreens !== 'object') {
        return false;
    }

    if (!['metric', 'imperial'].includes(s.unitSystem)) {
        return false;
    }

    // Validate each activity configuration
    const activityTypes: ActivityType[] = ['cycling', 'running', 'indoor', 'walking', 'custom'];
    return activityTypes.every((type) => {
        const config = s.activityScreens[type];
        return config ? validateActivityScreensConfig(config) : true; // Allow missing configs
    });
}

/**
 * Validate an activity screens configuration
 */
function validateActivityScreensConfig(config: unknown): config is ActivityScreensConfig {
    if (!config || typeof config !== 'object') {
        return false;
    }

    const c = config as { activityType?: unknown; screens?: unknown };

    if (typeof c.activityType !== 'string') {
        return false;
    }

    if (!Array.isArray(c.screens) || c.screens.length === 0) {
        return false;
    }

    return true;
}
