/**
 * Initialize Workout Selection Logic
 * Call this from the Workouts View
 */
export function initWorkoutSelectionLogic(): void {
    initPlayerControls(); // Logic only, assuming container exists
    initImportExport();
    refreshWorkoutList();
}

/**
 * Initialize Import/Export controls
 */
function initImportExport(): void {
    const importBtn = document.getElementById('importWorkoutBtn');
    const importInput = document.getElementById('importWorkoutInput') as HTMLInputElement;

    if (importBtn && importInput) {
        importBtn.addEventListener('click', () => {
            importInput.click();
        });

        importInput.addEventListener('change', (e) => {
            const files = (e.target as HTMLInputElement).files;
            if (files && files.length > 0) {
                importWorkoutFile(files[0]);
                // Reset input
                importInput.value = '';
            }
        });
    }
}

import { parseErgMrc } from '../workouts/parsers/ergMrcParser.js';

/**
 * Import workout from file (supports JSON, ERG, MRC formats)
 */
async function importWorkoutFile(file: File): Promise<void> {
    try {
        const text = await file.text();
        const lowerName = file.name.toLowerCase();
        let workout: StructuredWorkout;

        if (lowerName.endsWith('.erg') || lowerName.endsWith('.mrc')) {
            workout = parseErgMrc(text, file.name);
        } else if (lowerName.endsWith('.json')) {
            workout = JSON.parse(text) as StructuredWorkout;
            // Validate required fields
            if (!workout.name || !Array.isArray(workout.steps) || workout.steps.length === 0) {
                throw new Error('Invalid workout format: Missing name or steps');
            }
        } else {
            throw new Error('Unsupported file format. Please use .json, .erg, or .mrc files.');
        }

        // Ensure unique ID for imported workout
        if (!workout.id) {
            workout.id = crypto.randomUUID();
        }

        // Add imported tag if not already present
        const tags = new Set(workout.tags || []);
        tags.add('imported');
        workout.tags = Array.from(tags);

        saveCustomWorkout(workout);
        refreshWorkoutList();
        announce(`Imported workout: ${workout.name}`, 'assertive');
    } catch (err) {
        const errorMessage = err instanceof Error ? err.message : 'Unknown error';
        console.error('Import failed:', err);
        alert(`Failed to import workout: ${errorMessage}`);
        announce('Failed to import workout', 'assertive');
    }
}

/**
 * Export workout as JSON file
 */
function exportWorkout(workout: StructuredWorkout): void {
    try {
        const json = JSON.stringify(workout, null, 2);
        const dataStr = 'data:text/json;charset=utf-8,' + encodeURIComponent(json);
        const downloadAnchorNode = document.createElement('a');
        const fileName = `${workout.name.replace(/[^a-z0-9]/gi, '_').toLowerCase()}.json`;

        downloadAnchorNode.setAttribute('href', dataStr);
        downloadAnchorNode.setAttribute('download', fileName);
        downloadAnchorNode.style.display = 'none';

        document.body.appendChild(downloadAnchorNode);
        downloadAnchorNode.click();
        document.body.removeChild(downloadAnchorNode);
    } catch (err) {
        console.error('Export failed:', err);
        alert('Failed to export workout. Please try again.');
        announce('Failed to export workout', 'assertive');
    }
}

