/**
 * Render list of workouts
 * This function is exported so the Workout Builder can refresh the list after creating a workout
 */
export function refreshWorkoutList(): void {
    const container = document.getElementById('workoutList');
    if (!container) return;

    container.innerHTML = '';

    const custom = getCustomWorkouts();

    if (custom.length > 0) {
        const h3 = document.createElement('h3');
        h3.textContent = 'My Workouts';
        h3.style.margin = '0 0 8px 0';
        h3.style.fontSize = '1rem';
        h3.style.color = 'var(--color-text-secondary)';
        container.appendChild(h3);

        custom.forEach((w) => renderWorkoutItem(w, container, true));

        const hr = document.createElement('hr');
        hr.style.margin = '16px 0';
        hr.style.border = '0';
        hr.style.borderTop = '1px solid var(--color-border)';
        container.appendChild(hr);

        const h3lib = document.createElement('h3');
        h3lib.textContent = 'Library';
        h3lib.style.margin = '0 0 8px 0';
        h3lib.style.fontSize = '1rem';
        h3lib.style.color = 'var(--color-text-secondary)';
        container.appendChild(h3lib);
    }

    WORKOUT_LIBRARY.forEach((w) => renderWorkoutItem(w, container, false));
}

/**
 * Render a single workout item in the workout list
 * @param w - The workout to render
 * @param container - The container element to append the workout item to
 * @param isCustom - Whether this is a custom workout (enables delete functionality)
 */
function renderWorkoutItem(w: StructuredWorkout, container: HTMLElement, isCustom: boolean): void {
    const duration = getWorkoutDuration(w);

    const el = document.createElement('div');
    el.className = 'workout-item';
    el.setAttribute('role', 'button');
    el.setAttribute('tabindex', '0');
    el.setAttribute('data-workout-id', w.id);

    // Build action buttons HTML with consistent styling via CSS classes
    const exportButton = `<button class="workout-action-btn export-workout-btn" aria-label="Export ${escapeHtml(w.name)}" title="Export JSON"></button>`;
    const deleteButton = isCustom
        ? `<button class="workout-action-btn delete-workout-btn" aria-label="Delete ${escapeHtml(w.name)}" title="Delete"></button>`
        : '';

    el.innerHTML = `
        <div class="workout-item-actions">
            ${exportButton}
            ${deleteButton}
        </div>
        <div class="workout-item-header">
            <span class="workout-item-title">${escapeHtml(w.name)}</span>
        </div>
        <div class="workout-item-duration"> ${formatDuration(duration)}</div>
        <div class="workout-item-desc">${escapeHtml(w.description)}</div>
        <div class="workout-tags">
            ${(w.tags || []).map((t) => `<span class="workout-tag">${escapeHtml(t)}</span>`).join('')}
        </div>
    `;

    // Handle Export
    const exportBtn = el.querySelector('.export-workout-btn');
    if (exportBtn) {
        exportBtn.addEventListener('click', (e) => {
            e.stopPropagation();
            exportWorkout(w);
            announce(`Exporting workout: ${w.name}`, 'polite');
        });
    }

    // Handle Delete
    if (isCustom) {
        const delBtn = el.querySelector('.delete-workout-btn');
        if (delBtn) {
            delBtn.addEventListener('click', (e) => {
                e.stopPropagation();
                if (confirm(`Delete workout "${w.name}"?`)) {
                    try {
                        deleteCustomWorkout(w.id);
                        refreshWorkoutList();
                        announce(`Deleted workout: ${w.name}`, 'assertive');
                    } catch (error) {
                        console.error('Failed to delete workout:', error);
                        announce(`Failed to delete workout: ${w.name}`, 'assertive');
                    }
                }
            });
        }
    }

    // Start workout on click (prevent if clicking action buttons)
    const handleWorkoutStart = (e: Event): void => {
        // Don't start workout if clicking on action buttons
        const target = e.target as HTMLElement;
        if (target.tagName === 'BUTTON' || target.closest('button')) {
            return;
        }

        startStructuredWorkout(w);
        // Navigate to dashboard
        const router = (window as any).router;
        if (router && typeof router.navigate === 'function') {
            router.navigate('/');
        }
    };

    el.addEventListener('click', handleWorkoutStart);

    // Keyboard support
    el.addEventListener('keydown', (e) => {
        if (e.key === 'Enter' || e.key === ' ') {
            e.preventDefault();
            handleWorkoutStart(e);
        }
    });

    container.appendChild(el);
}

/**
 * Select and start a structured workout
 * @param workout - The workout to start
 * @param onComplete - Optional callback to execute when workout completes
 */
export function startStructuredWorkout(workout: StructuredWorkout, onComplete?: () => void): void {
    if (activeRunner) {
        if (!confirm('A workout is already active. Stop it and start this one?')) {
            return;
        }
        activeRunner.stop();
    }

    // Initialize audio on user interaction
    audioManager.init();

    // Ensure voice feedback loop is running
    voiceFeedback.startIntervalAnnouncements();

    activeRunner = new WorkoutRunner(workout);

    // Track if we have already called onComplete
    let completed = false;

    activeRunner.setCallback((state) => {
        updatePlayerUI(state);
        voiceFeedback.updateWorkoutState(state);
        musicController.onWorkoutStateChange(state);

        if (state.isFinished && !completed && onComplete) {
            completed = true;
            onComplete();
        }
    });

    // Show player
    const overlay = document.getElementById('workoutPlayerOverlay');
    if (overlay) {
        overlay.style.display = 'block';
        // playerVisible = true;
    }

    // specific Setup for Profile
    const profileEl = document.getElementById('wpWorkoutProfile');
    if (profileEl) {
        (profileEl as any).workout = workout;
    }

    // Announce
    announce(`Starting workout: ${workout.name}. Ready to start.`, 'assertive');
}

/**
 * Initialize player controls (Minimize, Close, Skip)
 */
function initPlayerControls(): void {
    const overlay = document.getElementById('workoutPlayerOverlay');
    const minimizeBtn = document.getElementById('wpMinimize');
    const closeBtn = document.getElementById('wpClose');
    const skipBtn = document.getElementById('wpSkip');

    if (!overlay) return;

    // Close/Stop
    closeBtn?.addEventListener('click', () => {
        if (!activeRunner) return;
        if (confirm('Stop current workout?')) {
            activeRunner.stop();
            activeRunner = null;
            voiceFeedback.updateWorkoutState(null);
            overlay.style.display = 'none';
            announce('Workout stopped', 'polite');
        }
    });

    // Minimize (Toggle visibility of body)
    minimizeBtn?.addEventListener('click', () => {
        const body = overlay.querySelector('.workout-player-body') as HTMLElement;
        if (body) {
            const isHidden = body.style.display === 'none';
            body.style.display = isHidden ? 'block' : 'none';
            minimizeBtn.textContent = isHidden ? '_' : '';
        }
    });

    // Skip
    skipBtn?.addEventListener('click', () => {
        if (activeRunner) {
            activeRunner.nextStep();
            announce('Skipped to next step', 'polite');
        }
    });
}

/**
 * Update the player UI based on the current workout state
 * @param state - The current active workout state
 */
function updatePlayerUI(state: ActiveWorkoutState): void {
    const nameEl = document.getElementById('wpWorkoutName');
    const descEl = document.getElementById('wpStepDescription');
    const timeEl = document.getElementById('wpStepTime');
    const durEl = document.getElementById('wpStepDuration');
    const targetEl = document.getElementById('wpTargetPower');
    const nextEl = document.getElementById('wpNextStep');
    const progressEl = document.getElementById('wpProgress');

    if (nameEl) nameEl.textContent = state.workout.name;

    if (state.currentStep) {
        if (descEl)
            descEl.textContent =
                state.currentStep.description || state.currentStep.name || state.currentStep.type.toUpperCase();

        // Timer
        const timeRemaining = state.currentStep.duration - state.stepElapsedTime;
        if (timeEl) timeEl.textContent = formatTime(Math.max(0, timeRemaining));
        if (durEl) durEl.textContent = formatTime(state.currentStep.duration);

        // Target
        if (targetEl) {
            targetEl.textContent = state.currentAbsoluteTarget?.value
                ? Math.round(state.currentAbsoluteTarget.value).toString()
                : '--';
        }

        // Visualizer
        const visualizer = document.getElementById('wpTargetVisualizer');
        if (visualizer && state.currentAbsoluteTarget?.value) {
            visualizer.setAttribute('target', state.currentAbsoluteTarget.value.toString());

            // Get actual power from global state
            try {
                const appState = getState();
                const power = appState.measurementsState.power;
                const currentPower = power.length > 0 ? power[power.length - 1].value : 0;
                visualizer.setAttribute('actual', currentPower.toString());
            } catch (e) {
                // Ignore if state not ready
            }
        }

        // Profile Update
        const profileEl = document.getElementById('wpWorkoutProfile');
        if (profileEl) {
            profileEl.setAttribute('current-time', state.totalElapsedTime.toString());
        }
    }

    // Next Step
    if (nextEl) {
        if (state.nextStep) {
            const nextPwr = calculateNextTarget(state.nextStep);
            nextEl.textContent = `${state.nextStep.type} (${formatTime(state.nextStep.duration)}${nextPwr})`;
        } else {
            nextEl.textContent = 'Finish';
        }
    }

    // Progress Bar
    if (progressEl) {
        const pct = (state.stepElapsedTime / state.currentStep.duration) * 100;
        progressEl.style.width = `${Math.min(100, Math.max(0, pct))}%`;

        // Change color based on zone/intensity?
        // simple: blue for warmup/rest, orange for active
        if (state.currentStep.type === 'active') {
            progressEl.style.backgroundColor = '#ff9800'; // Orange
        } else if (state.currentStep.type === 'rest' || state.currentStep.type === 'warmup') {
            progressEl.style.backgroundColor = '#2196F3'; // Blue
        } else {
            progressEl.style.backgroundColor = '#4CAF50'; // Green
        }
    }

    // Finished?
    if (state.isFinished) {
        const overlay = document.getElementById('workoutPlayerOverlay');
        if (overlay) overlay.style.display = 'none';
        activeRunner = null;
        alert('Workout Complete!');
        announce('Workout Complete!', 'assertive');
    }
}

/**
 * Format seconds to mm:ss format
 * @param seconds - The number of seconds to format
 * @returns Formatted time string in mm:ss format
 */
function formatTime(seconds: number): string {
    const m = Math.floor(seconds / 60);
    const s = seconds % 60;
    return `${m.toString().padStart(2, '0')}:${s.toString().padStart(2, '0')}`;
}

/**
 * Calculate the next step target for display
 * @param step - The workout step to calculate target for
 * @returns Formatted target string
 */
function calculateNextTarget(step: WorkoutStep): string {
    const t = step.target;
    if (!t) return '';
    return t.value ? ` @ ~${t.value}${t.unit === 'percent_ftp' ? '%' : 'W'}` : '';
}

/**
 * Hook to link main timer start to workout runner
 * Call this from main.ts or connection logic when recording starts
 */
export function onWorkoutStart(): void {
    if (activeRunner) {
        activeRunner.start();
    }
}

/**
 * Hook to link main timer pause to workout runner
 * Call this from main.ts or connection logic when recording pauses
 */
export function onWorkoutPause(): void {
    if (activeRunner) {
        activeRunner.pause();
    }
}
